}

.download-resume {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: #141414;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  transition-duration: .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 20px #0000002a;
}

.cloud-download {
  width: 16px;
  transition-duration: .3s;
}

.download-resume:hover {
  width: 110px;
  background-color: #ff4545;
  border-radius: 34px;
  align-items: center;
  transition-duration: .3s;
}

.download-resume:hover .cloud-download {
  width: 50px;
  transition-duration: .3s;
  transform: translateY(86%);
}

.download-resume:before {
  content: "Download Resume PDF";
  color: #fff;
  font-size: 2px;
  transition-duration: .3s;
  position: absolute;
  top: -28px;
}

.download-resume:hover:before {
  opacity: 1;
  font-size: 13px;
  transition-duration: .3s;
  transform: translateY(30px);
}

